import React, { useState } from 'react';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
export type model = {
    number?: string;
}
type BoxsType = {
    model: model[];
}
const Boxs = (props: BoxsType) => {
    const [color, setcolor] = useState<Number>(-1);
    const onClick = (index: number) => {
        debugger
        setcolor(index);
    }
    var settings = {
        dots: false,
        infinite: false,
        speed: 500,
        slidesToShow: 7,
        slidesToScroll: 1,
    }
    return (
        <div className='w-11/12'>
            <Slider {...settings}>
                {props.model.map((item, index) => {
                    return (
                        <div className='p-8'>
                            <div style={{ background: color === index ? 'blue' : 'white' }} onClick={() => onClick(index)} className='rounded-full pr-3 pl-3 pt-12 pb-12'>
                                <p style={{ color: color === index ? 'white' : ' gray' }} className='text-center text-3xl font-bold'>{item.number}</p>
                                <p style={{ color: color === index ? 'white' : ' gray' }} className='text-center text-2xl '>week</p>
                            </div>
                        </div>
                    )
                })}
            </Slider>
        </div>
    );
};

export default Boxs;